import React, { Component } from 'react';
import { Row, Form, Input } from 'antd';
import { BtnSave, BtnLock, BtnExport } from 'components/common/button';
import { TableForm } from 'components/common';

@Form.create({ name: 'formTableQualifiedDoThesis' })
class TableQualifiedDoThesis extends Component {
  constructor(props) {
    super(props);
    this.refTable = React.createRef();
    this.state = {};
  }

  getTableData = () => {
    return this.refTable.current.getTableData();
  }

  createColumns = () => {
    const columns1 = [
      {
        title: 'M sinh vin',
        dataIndex: 'studentCode',
        key: 'studentCode',
      },
      {
        title: 'H v tn',
        dataIndex: 'fullName',
        key: 'fullName',
      },
      {
        title: 'Ngy sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'Gii tnh',
        dataIndex: 'genderText',
        key: 'genderText',
      },
      {
        title: 'Ni sinh',
        dataIndex: 'pobText',
        key: 'pobText',
      },
      {
        title: 'Chuyn ngnh',
        dataIndex: 'studyFieldLabel',
        key: 'studyFieldLabel',
      },
      {
        title: 'Tn lp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'DXLTN4',
        dataIndex: 'totalCourseIn4',
        key: 'totalCourseIn4',
      },
      {
        title: 'DXLTN10',
        dataIndex: 'totalCourse',
        key: 'totalCourse',
      },
      {
        title: 'Xp hng',
        dataIndex: 'graduateClassificationLabel',
        key: 'graduateClassificationLabel',
      },
      {
        title: 'Nm xt',
        dataIndex: 'yearEdu',
        key: 'yearEdu',
      },
      {
        title: 't xt',
        dataIndex: 'sessionNo',
        key: 'sessionNo',
      },
    ];
    const columns2 = [
      {
        title: 'Ghi ch',
        dataIndex: 'reason',
        key: 'reason',
        render: (_text, record) => {
          const { getFieldDecorator } = this.props.form;
          return (
            <Form.Item>
              {getFieldDecorator(`reason-${record.autoKey}`, {
                initialValue: record.reason,
                rules: [
                  {
                    required: true,
                    message: 'Vui lng in ghi ch!',
                  },
                ],
              })(
                <Input />
              )}
            </Form.Item>
          );
        },
      },
    ];
    return columns1.concat(columns2);
  };

  render() {
    const {
      reload,
      rowKey,
      data,
      handleSelectedRows,
      form,
      createUpdateListStudentCheckGraduate,
      createUpdateListStudentCheckNoGraduate,
      exportPDF,
      exportExcel,
      disableJoinBtn,
      disableExport
    } = this.props;
    const columns = this.createColumns();
    return (
      <div>
        <Row>
          <div className="btn-group" style={{ float: 'right' }}>
            <BtnSave
              title="Xt tt nghip"
              onClick={createUpdateListStudentCheckGraduate}
              disabled={disableJoinBtn}
            />
            <BtnLock
              icon="exclamation-circle"
              title="Cha  iu kin"
              onClick={createUpdateListStudentCheckNoGraduate}
              disabled={disableJoinBtn}
            />
            <BtnExport
              title="Xut Excel"
              onClick={exportExcel}
              disabled={disableJoinBtn || disableExport}
            />
            <BtnExport
              title="Xut PDF"
              onClick={exportPDF}
              disabled={disableJoinBtn || disableExport}
            />
          </div>
        </Row>
        <TableForm
          reload={reload}
          data={data}
          handleSelectedRows={handleSelectedRows}
          rowKey={rowKey}
          columns={columns}
          form={form}
          ref={this.refTable}
        />
      </div>
    );
  }
}

export default TableQualifiedDoThesis;



